import React, { useState } from "react";
import "./Nav.scss";
import { NavLink } from "react-router-dom";
import downIcon from "./../../../img/icons/down.webp";

const Nav = ({ active, setActive }) => {
    const [drop, setDrop] = useState(false);
    return (
        <nav className={active ? " navbar active" : "navbar"}>
            <ul>
                <li onClick={()=>setActive(!active)}>
                    <NavLink to="/Home"> </NavLink>
                </li>
                <li onClick={()=>setActive(!active)}>
                    <NavLink to="/Services"> </NavLink>
                </li>
                {/* <li onClick={()=>setActive(!active)}>
                    <NavLink to="/Gallery"> </NavLink>
                </li> */}
                <li>
                    <button onClick={()=>setDrop(!drop)}>
                         <span>new</span>
                        <img src={downIcon} alt="downIcon" />
                    </button>
                    <ul
                        className={drop ? "nav-list-open" : "nav-list-closed"}
                        onMouseLeave={()=>setDrop(!drop)}
                    >
                        <li
                            onClick={() => {
                                setDrop(!drop);
                                setActive(!active)
                            }}
                        >
                            <NavLink
                                className="dropdown-item"
                                to="/Diagnostics/Starter"
                            >
                                
                            </NavLink>
                        </li>
                        <li
                            onClick={() => {
                                setDrop(!drop);
                                setActive(!active)
                            }}
                        >
                            <NavLink
                                className="dropdown-item"
                                to="/Diagnostics/Altenator"
                            >
                                
                            </NavLink>
                        </li>
                        <li
                            onClick={() => {
                                setDrop(!drop);
                                setActive(!active)
                            }}
                        >
                            <NavLink
                                className="dropdown-item"
                                to="/Diagnostics/Battery"
                            >
                                
                            </NavLink>
                        </li>
                        <li
                            onClick={() => {
                                setDrop(!drop);
                                setActive(!active)
                            }}
                        >
                            <NavLink
                                className="dropdown-item"
                                to="/Diagnostics/All"
                            >
                                  
                            </NavLink>
                        </li>
                        <li
                            onClick={() => {
                                setDrop(!drop);
                                setActive(!active)
                            }}
                        >
                            <NavLink
                                className="dropdown-item"
                                to="/Diagnostics/Test/7"
                            >
                                 
                            </NavLink>
                        </li>
                    </ul>
                </li>
                <li onClick={()=>setActive(!active)}>
                    <NavLink to="/Blogs"> </NavLink>
                </li>
                <li onClick={()=>setActive(!active)}>
                    <NavLink to="/ontact"></NavLink>
                </li>
            </ul>
        </nav>
    );
};

export default Nav;
