export const roadFotos = (): roadFotosType => {
    return serverData.roadFotos;
};
export const services = (): servicesType => {
    return serverData.services;
};
export const blogs = (): blogsType => {
    return serverData.blogs;
};
export const stFotos = (): roadFotosType => {
    return serverData.stFotos;
};
export const altFotos = (): roadFotosType => {
    return serverData.altFotos;
};
export const chFotos = (): roadFotosType => {
    return serverData.chFotos;
};
export const banner=() : slaidesType=>{
    return serverData.banner;
}

type dataDia = {
    texts: textType;
    cards: CardType;
};

export const diagnostics = (page: string): dataDia => {
    const Cards=()=>{
        if(page==='All'){
            return [...serverData.diagnostics.cards.Starter,...serverData.diagnostics.cards.Altenator,...serverData.diagnostics.cards.Battery]
        }else{
            //@ts-ignore
            return serverData.diagnostics.cards[page]
        }
    }
    // debugger
    const data:dataDia = {
        //@ts-ignore
        texts:serverData.diagnostics.texts[page],
        //@ts-ignore
        cards:Cards()
    };
    return data;
};
export default serverData;
